import React, { useState, useEffect } from "react";
import { Link, useParams, useNavigate } from "react-router-dom";
import Headertwo from "../components/Headertwo";
import { Footer } from "../components/Footer";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import MediaRenderer from "../components/MediaRenderer";
import image1 from "../assets/images/image1.jfif";
import image2 from "../assets/images/image2.jfif";
import image3 from "../assets/images/image4.jfif";
import image4 from "../assets/images/image5.jfif";
import image5 from "../assets/images/angular.jpg";
import image6 from "../assets/images/image3.png";
import axios from "axios";
import { useTranslation } from "react-i18next";
import i18n from "../i18n";
export default function Groupimages() {
  const navigate = useNavigate();
  const [modalOpen, setModalOpen] = useState(false);
  const [currentSlideIndex, setCurrentSlideIndex] = useState(1);
  const [isGrouppost, setGrouppost] = useState([]);
  const [groupId, setgroupId] = useState("");
  const [isGrouppostvideo, setGrouppostvideo] = useState([]);
  const [LoginData, setLoginData] = useState(null);
  const [Isslug, setSlug] = useState("");
  const { slug } = useParams();
  const [groupdetail, setgroupdetail] = useState("");
  const images = [
    {
      src: image1,
      wideSrc: image1,
      alt: "Nature",
    },
    {
      src: image2,
      wideSrc: image3,
      alt: "Snow",
    },
    {
      src: image4,
      wideSrc: image5,
      alt: "Mountains",
    },
    {
      src: image6,
      wideSrc: image6,
      alt: "Lights",
    },
    {
      src: image1,
      wideSrc: image1,
      alt: "Nature",
    },
    {
      src: image2,
      wideSrc: image3,
      alt: "Snow",
    },
    {
      src: image4,
      wideSrc: image5,
      alt: "Mountains",
    },
    {
      src: image6,
      wideSrc: image6,
      alt: "Lights",
    },
  ];
  const { t } = useTranslation();

  useEffect(() => {
    const savedLanguage = localStorage.getItem("language") || "en"; // Default to 'en' if no language is saved
    console.log("language");
    console.log(savedLanguage);

    // Change the language if it's not already the current language
    if (i18n.language !== savedLanguage) {
      i18n.changeLanguage(savedLanguage);
    }
  }, [i18n, t]);
  const openModal = () => setModalOpen(true);
  const closeModal = () => setModalOpen(false);

  useEffect(() => {
    if (modalOpen) {
      // Disable scrolling when modal is open
      document.body.style.overflowY = "hidden";
    } else {
      // Enable scrolling when modal is closed
      document.body.style.overflowY = "auto";
    }

    // Cleanup to restore the overflow style when the component unmounts
    return () => {
      document.body.style.overflowY = "auto";
    };
  }, [modalOpen]);
  useEffect(() => {
    const handleKeyDown = (event) => {
      if (!modalOpen) return; // Ensure key events work only when modal is open

      if (event.key === "ArrowRight") {
        showSlide(currentSlideIndex + 1); // Right arrow → moves to next slide
      } else if (event.key === "ArrowLeft") {
        showSlide(currentSlideIndex - 1); // Left arrow ← moves to previous slide
      }
    };

    document.addEventListener("keydown", handleKeyDown);

    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  }, [currentSlideIndex, modalOpen]);
  useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.key === "Escape") {
        setModalOpen(false);
      }
    };

    // Add event listener
    window.addEventListener("keydown", handleKeyDown);

    // Cleanup event listener on unmount
    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, []);
  var apiUrl = "https://backend.amourette.no/api/groups/";
  useEffect(() => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null; // Safely parse login data
    setLoginData(userLogin);
  }, []);
  useEffect(() => {
    if (slug !== undefined) {
      setSlug(slug); // Ensure setSlug is defined and does not cause issues
      getDetails(slug);
    }
  }, [LoginData, slug]);
  useEffect(() => {
    if (LoginData && groupId) {
      getpostComment(groupId);
    }
  }, [LoginData, groupId]);
  const getDetails = async (slug) => {
    let formData = {
      slug: slug,
    };

    try {
      const res = await axios.post(apiUrl + "getGroupDetailSlug", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.group;
      console.log(respo);
      console.log("vh");
      setgroupdetail(respo);

      if (res.data.group === "" || res.data.group === undefined) {
        navigate("/groups");
      } else {
        console.log(LoginData);
      }
      setgroupId(respo.id);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        //console.error("Request data:", err.request);
      } else {
        // console.error("Error message:", err.message);
      }
    }
  };
  const getpostComment = async (idd) => {
    let formData = {
      group_id: idd,
      user_id: LoginData.id,
    };
    console.log(formData);
    try {
      const res = await axios.post(apiUrl + "get_postComment", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      console.log("res.data");
      const images = [];
      const videos = [];
      if (res.data.results.length > 0) {
        res.data.results.forEach((item) => {
          const file = item.file; // Assuming 'file' contains the file URL

          if (file && typeof file === "string") {
            // Ensure file exists and is a string
            images.push(item);
            if (file.match(/\.(jpeg|jpg|gif|png|webp|bmp|svg)$/i)) {
              images.push(item);
            } else if (
              file.match(/\.(mp4|mov|avi|mkv|webm|ogg|flv|3gp|webm)$/i)
            ) {
              videos.push(item);
            }
          } else {
            console.warn("Invalid file URL:", file); // Debugging log
          }
        });
      }

      setGrouppost(res.data.results);
      setGrouppostvideo(videos);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const validPosts = isGrouppost.filter((post) => post.file);
  const showSlide = (index) => {
    if (index < 0) {
      setCurrentSlideIndex(validPosts.length); // Loop back to last slide
    } else if (index > validPosts.length) {
      setCurrentSlideIndex(1); // Loop to first slide
    } else {
      setCurrentSlideIndex(index);
    }
  };
  console.log(isGrouppost);
  return (
    <>
      <Headertwo />
      <section className="ggboxes d-block gapy">
        <div className="container-lg">
          <div className="row gy-4 mb-5">
            <div className="col-md-6">
              <div className="d-flex justify-content-start gap-2">
                <h1 className="pagetitle">{`${t("Photos")}`}</h1>
              </div>
            </div>
            <div className="col-md-6">
              <div className="d-flex justify-content-md-end"></div>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <Tabs>
                <TabList>
                  <Tab>{`${t("Media")}`}</Tab>
                  <button
                    type="button"
                    onClick={() => navigate(`/group/${slug}`)}
                    className="btn backbtn"
                  >
                    Back
                  </button>
                  {/* <Tab>{`${t("Album")}`}</Tab> */}
                </TabList>

                <TabPanel>
                  <div className="row gy-4">
                    {isGrouppost.length > 0 ? (
                      isGrouppost.map(
                        (post, index) =>
                          post.file && typeof post.file === "string" ? (
                            <div className="col-md-3" key={index}>
                              <div className="d-flex flex-column gap-2">
                                <div className="overflow-hidden rounded-3 d-block">
                                  <MediaRenderer
                                    url={post.file}
                                    onClick={() => {
                                      openModal();
                                      showSlide(index + 1);
                                    }}
                                  />
                                </div>
                                <Link
                                  to={`/friend/${post.uid}`}
                                  target="_blank"
                                  className="goupmediatext"
                                >
                                  {post.username}
                                </Link>
                              </div>
                            </div>
                          ) : null // <-- Skip rendering if no valid media file
                      )
                    ) : (
                      <p>{`${t("No_available_media")}`}</p>
                    )}
                  </div>
                </TabPanel>
                <TabPanel>
                  <div className="row gy-4">
                    {isGrouppost.length > 0 ? (
                      isGrouppost.map((post, index) => (
                        <div className="col-md-3" key={index}>
                          <div className="d-flex flex-column gap-2">
                            <div className="ggimagebox ofit">
                              <video
                                onClick={() => {
                                  openModal();
                                  showSlide(index + 1);
                                }}
                                controls
                                alt="video"
                              >
                                <source src={post.file} type="video/mp4" />
                                {`${t("Your_browser_does")}`}
                              </video>
                            </div>
                            {/* <h6 className="urnamgg">{post.name}</h6> */}
                          </div>
                        </div>
                      ))
                    ) : (
                      <p>{`${t("No_avalibale_media")}`}.</p>
                    )}
                  </div>
                </TabPanel>
                {/* <TabPanel>
                  <h2>Any content 3</h2>
                </TabPanel> */}
              </Tabs>
            </div>
          </div>
        </div>
      </section>

      {/* pop up code */}
      {modalOpen && (
        <div id="myModal" className="modalgg">
          <span className="close cursor" onClick={closeModal}>
            &times;
          </span>
          <div className="modal-content">
            {validPosts.length > 0 ? (
              validPosts.map((post, index) => (
                <div
                  className="mySlides"
                  style={{
                    display: currentSlideIndex === index + 1 ? "block" : "none",
                  }}
                  key={index}
                >
                  <div className="numbertext">
                    {index + 1} / {validPosts.length}
                  </div>
                  <MediaRenderer url={post.file} />
                </div>
              ))
            ) : (
              <p>{`${t("No_available_media")}`}</p>
            )}

            <a
              className="prev"
              onClick={() => showSlide(currentSlideIndex - 1)}
            >
              &#10094;
            </a>
            <a
              className="next"
              onClick={() => showSlide(currentSlideIndex + 1)}
            >
              &#10095;
            </a>
          </div>
        </div>
      )}
      {/* pop up code */}
      <Footer />
    </>
  );
}
